<template>
  <div>

    <a-button @click="visible = true">显示</a-button>

    <div>选中的项：</div>
    <a-tag v-for="(item, index) in selectValue" :key="index">{{ item.name }}</a-tag>

    <select-user      loadDataType="user" v-model="selectValue" class="select-user" is-sync :replaceFields="{ children: 'children' }" @change="handleChange" :loadData="loadData" @ok="handleOk" :visible.sync="visible" dataType="user" />

  </div>
</template>

<script>
import { SelectUser } from '@handday/components'

export default {
  name: 'SelectUsers',
  components: {
    SelectUser
  },
  data () {
    return {
      loadData: null,
      visible: false,
      selectValue: [],
      select: null
    }
  },
  mounted() {
    this.handleAddEmps()
  },
  methods: {
    handleOk(result, closeFn) {
      console.log(result)
    },
    handleChange(result, selectList) {
      console.log(result)
    },
    handleClose(item) {

      console.log(item)
      this.selectValue = this.selectValue.filter(o => o.selectId !== item.selectId)
    },
    /**
     * 添加负责人弹窗开启
     * */
    handleAddEmps (item) {
      this.loadData = (node) => {
        console.log(typeof(node))
        return new Promise((resolve, reject) => {
          const length = Math.floor(Math.random() * 10 + 1)
          const getData = (len) => {
            let data = []
            for(let i = 0; i < len; i++) {
              const id = `${Math.floor(Math.random() * 1000000000000)}`
              const type = Math.floor(Math.random() * 10) % 2
              if (type) {
                data.push({
                  corpId: 10001,
                  departmentId: id,
                  parentId: item && item.parentId || 0,
                  level: item && item.level + 2 || 1,
                  name: `${Math.floor(Math.random() * 100000)}部`
                })
              } else {
                data.unshift({
                  name: `${Math.floor(Math.random() * 100000)}人`,
                  businessId: id,
                  avatar: 'https://t7.baidu.com/it/u=1595072465,3644073269&fm=193&f=GIF'
                })
              }
            }
            const parent = {
              corpId: 10001,
              departmentId: `${Math.floor(Math.random() * 1000000000000)}`,
              parentId: 0,
              level: item && item.level || 1,
              name: `1部`,
              children: data
            }
            if (typeof(node) === 'string') {
              return [parent]
            }
            return data
          }
          setTimeout(() => {
            resolve(getData(length))
          }, 1000)
        })
      }
    }
  }
}
</script>

